@import "../../global.scss";

.testimonials {
  display: flex;
  flex-direction: column;
  align-items: center;

  // @include mobile{
  //   justify-content: space-around;
  // }


  h1 {
    font-size: 5.3vh;
    padding-top: 4vh;
    // @include mobile{
    //   font-size: 20px;
    // }
  }

  .container {
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;

    @include mobile{
      flex-wrap: wrap;

    }

    .card {
      width: 250px;
      height: 70%;
      border-radius: 10px;
      box-shadow: 0px 0px 15px -8px black;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      padding: 2.5vh;
      transition: all 1s ease;

      // @include mobile{
      //   height: 180px;
      //   margin: 10px 0;
      // }

      &.featured{
          width: 300px;
          height: 75%;
          margin: 0 4vh;

          // @include mobile{
          //   width: 250px;
          //   height: 180px;
          //   margin: 1px;
          // }
      }

      &:hover{
          transform: scale(1.1);
      }

      .top {
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          &.left,
          &.right {
            height: 3vh;
          }
          &.user {
            height: 8vh;
            width: 60px;
            border-radius: 50%;
            object-fit: cover;
            margin: 0 4vh;

            // @include mobile{
            //   width: 30px;
            //   height: 30px;
            // }
          }
        }
      }

      .center {
        padding: 1.5vh;
        border-radius: 10px;
        background-color: rgb(250, 244, 245);
        font-size: 2.3vh;

        // @include mobile{
        //   font-size: 12px;
        //   padding: 5px;
        // }
      }
      .bottom {
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: 2.5vh;
        justify-content: center;

        h3 {
          margin-bottom: 1vh;

          // @include mobile{
          //   font-size: 14px;
          // }
        }
        h4{
            color: rgb(121, 115, 115);
            // @include mobile{
            //   font-size: 13px;
            // }
        }
      }
    }
  }
}
